import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreDocument, AngularFirestoreCollection } from '@angular/fire/firestore';
import { Observable } from 'rxjs';
import { map } from 'rxjs/operators';
import { AuthService } from 'src/app/auth/auth.service';
import * as _ from 'underscore';

@Injectable({
  providedIn: 'root'
})
export class RealestateService {
  realestates: AngularFirestoreCollection;
  myRealestates: any;
  allRealEstates: any;
  currentRealestate: any;
  $realestateSub: any;
    // View States:
    add = false;
    edit = false;

  public title: any = null;
  constructor(private db: AngularFirestore, private authService: AuthService) {
   }
  getMyRealestates() {
    const user = JSON.parse(sessionStorage.getItem('user'));
    const userRef = user.uid;
    return this.db.collection('realestates', ref => ref.where('ownerID', '==', userRef)).valueChanges();
  }

  getAllRealestates() {
    return this.allRealEstates = this.db.collection('realestates').valueChanges();
  }

  getRealestate(realestateId: string) {
    return this.db.collection('realestates').doc(`${realestateId}`);
  }

  getRealestateSubscription(realestateId: string) {
    if (!_.isEmpty(this.$realestateSub)) {
      this.$realestateSub.unsubscribe();
    }
    this.$realestateSub = this.db.doc(`realestates/${realestateId}`).snapshotChanges().subscribe(
      {
        next: response => {
          const data = response.payload.data();
          const id = response.payload.id;
          this.currentRealestate = {id, ...data};
        }
      }
    );
  }
}
