import { Component, OnInit, Input } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import { LoaderService } from '@app/_services/loader.service';
import { AuthService } from '@app/auth/auth.service';
import { GlobalService } from '@app/_services/global.service';

@Component({
  selector: 'app-suborder-list',
  templateUrl: './suborder-list.component.html',
  styleUrls: ['./suborder-list.component.css']
})
export class SuborderListComponent implements OnInit {
  // @Input() suborderList: []; // package list to render
  @Input() orderId: string; // id of the parent order
  suborder_state: string; // initial state i.e list
  // userRef: any;

  constructor(
    private os: OrdersService,
    public auth: AuthService,
    public loader: LoaderService,
    public gs: GlobalService) {
  }

  ngOnInit() {
    this.suborder_state = 'list-view';
    if (this.auth.role !== 'Dienstleister') {
      this.loader.getSubordersID(this.orderId);
    }
  }
}
