import { Component, OnInit, Input, ViewChild } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import * as _ from 'underscore';
import { AuthService } from '@app/auth/auth.service';
import { RealestateService } from '@app/_services/realestate.service';
import { UsersService } from '@app/_services/users.service';
import { UploadService } from '@app/_services/upload.service';
import { GlobalService } from '@app/_services/global.service';

@Component({
  selector: 'app-suborder-overview',
  templateUrl: './suborder-overview.component.html',
  styleUrls: ['./suborder-overview.component.css']
})
export class SuborderOverviewComponent implements OnInit {
  @Input() suborderId: string;
  @Input() realestateId: string;
  @Input() createdById: string;
  @Input() assignedTo: string;
  @Input() parentContext: any;

  createdByUserData: any;
  requestServiceProvider: any;
  submitDisabled = false;

  constructor(
    private os: OrdersService,
    public auth: AuthService,
    public res: RealestateService,
    public us: UsersService,
    private ups: UploadService,
    public gs: GlobalService
  ) {
  }

  async ngOnInit() {
    // fetch current suborder details on initialize
    // only fetch if the currently fetched suborder is different from the current
    if ((!_.isEmpty(this.os.currentSuborder) &&
      this.os.currentSuborder.id !== this.suborderId) ||
      _.isEmpty(this.os.currentSuborder)) {
      await this.os.getSuborderDetails(this.suborderId);
    }
    // fetch current realestate detail on intiailize
    if (!_.isEmpty(this.realestateId)) {
      this.res.getRealestateSubscription(this.realestateId);
    }
    // fetch current service provider detail on intiailize
    if (!_.isEmpty(this.assignedTo)) {
      this.requestServiceProvider = this.us.getUserDetails(this.assignedTo);
    }
    // fetch current realestate detail on intiailize
    if (!_.isEmpty(this.createdById)) {
      this.createdByUserData = this.us.getUserDetails(this.createdById);
    }
  }

  /**
 * Gets triggered when service provider requests the order. The order gets assigned to current logged in service provider
 */
  async requestOrder(orderIdParam: string) {
    const confirmed = confirm('Sie verpflichten Sich hiermit, sollten wir Ihnen den Auftrag nach einer Prfung durch uns final erteilen, diesen zu dem vereinbarten Auftragshonorar zu erledigen.');
    if (confirmed) {
      try {
        await this.os.assignSuborder(orderIdParam, this.auth.userData.uid);
      } catch (e) {
        alert(e);
      }
    } else {
    }
  }

  /**
 * Gets triggered when service provider completes the order.
 */
  async completeOrder(orderIdParam: string) {
    const confirmed = confirm('Sind Sie sicher, dass Sie diesen Auftrag final abschlieen mchten?');
    if (confirmed) {
      try {
        let status = this.auth.myUserObservable.role === 'Administrator' ? 'completed' : 'uploaded';
        await this.os.changeSuborderStatus(orderIdParam, status);
      } catch (e) {
        alert(e);
      }
    } else {
    }
  }

  /**
   * Disable the submit button "Auftrag abschlieen" for dienstleister depending on suborder type
   * add your conditions for enabling/disabling inside this function based on your suborder type
   */
  isSubmitDisabled() {
    if (this.submitDisabled) {
      return true;
    }
    return false;
  }

  setSubmitDisabled(value: boolean) {
    this.submitDisabled = value;
  }

  /**
 * change suborder status for Admin.
 */
  async changeSuborderStatus(orderIdParam: string, status) {
    await this.os.changeSuborderStatus(orderIdParam, status);
    // additional actions for dronemedia suborder
    if (this.os.currentSuborder.orderType === 'dronemedia' && status === 'completed') {
      // update pending retoucing suborder status
      this.os.updatePendingSuborder(this.os.currentSuborder.id);
    }
  }

  /**
 * Gets triggered when service provider requests the order. The order gets assigned to current logged in service provider
 */
  async acceptRequest(orderIdParam: string, response: boolean) {
    try {
      await this.os.acceptRequest(orderIdParam, response);
    } catch (e) {
      alert(e);
    }
  }

  ngOnDestroy() {
    // unsubscripte the obeservables when the view is destroyed
    if (!_.isEmpty(this.os.$suborderSub)) {
      this.os.$suborderSub.unsubscribe();
      this.os.currentSuborder = null;
    }

    if (!_.isEmpty(this.res.$realestateSub)) {
      this.res.$realestateSub.unsubscribe();
      this.res.currentRealestate = null;
    }
  }

}
