import { Injectable } from '@angular/core';
import { AngularFirestore } from '@angular/fire/firestore';
import * as firebase from 'firebase/app';
import { AngularFireStorage, AngularFireStorageReference } from '@angular/fire/storage';
import { Observable } from 'rxjs';

@Injectable({
  providedIn: 'root'
})
export class UsersService {
  public allUsers: any;
  public userDetails: any
  public tutorialSkipped = false;
  fileRef: AngularFireStorageReference;
  cartItems: any[] = [];
  selectedServices: any[] = [];

  constructor(private afs: AngularFirestore,
    private afStorage: AngularFireStorage) { }

  getAllUsers(): Observable<any> {
    return this.afs.collection('users').valueChanges();
  }

  getUserDetails(uid: string) {
    return this.afs.collection('users').doc(uid).valueChanges();
  }

  setVerified(uid: string) {
    return this.afs.collection('users').doc(uid).update({
      pilotVerified: true,
      verificationNeeded: false
    });
  }

  updateUser(uid: string, data: object) {
    return this.afs.collection('users').doc(uid).update(data);
  }

  deleteDoc(doc: string, uid: string) {
    const confirmed = confirm('Das Dokument wird aus dem Profil gelscht und der Nutzer auf unverifiziert gesetzt. Er kann die Plattform dann nicht mehr nutzen, falls er vorher bereits verifiziert war. Bitte nur nutzen, wenn bei der anfnglichen Verifikation falsche oder ungltige Dokumente hochgeladen wurden! Nach dem Lschvorgang wird der Nutzer darber informiert, dass die Verifikation gescheitert ist und er betreffende Dokumente neu hochladen muss.');
    if (confirmed) {
      let docName = '';
      switch (doc) {
        case 'doc0':
          docName = 'vertragliche-vereinbarung';
          break;
        case 'doc1':
          docName = 'haftpflicht';
          break;
        case 'doc2':
          docName = 'kenntnisnachweis';
          break;
        case 'doc3':
          docName = 'aufstiegsgenehmigung';
          break;
      }
      this.afStorage.ref(`users/${uid}/${docName}`).delete();
      if (doc === 'doc0') {
        return this.afs.collection('users').doc(uid).update({
          verificationDocument0: firebase.firestore.FieldValue.delete(),
          pilotVerified: false,
          verificationNeeded: false,
          verificationFailed: true
        });
      } else if (doc === 'doc1') {
        return this.afs.collection('users').doc(uid).update({
          verificationDocument1: firebase.firestore.FieldValue.delete(),
          pilotVerified: false,
          verificationNeeded: false,
          verificationFailed: true
        });
      } else if (doc === 'doc2') {
        return this.afs.collection('users').doc(uid).update({
          verificationDocument2: firebase.firestore.FieldValue.delete(),
          pilotVerified: false,
          verificationNeeded: false,
          verificationFailed: true
        });
      } else if (doc === 'doc3') {
        return this.afs.collection('users').doc(uid).update({
          verificationDocument3: firebase.firestore.FieldValue.delete(),
          pilotVerified: false,
          verificationNeeded: false,
          verificationFailed: true
        });
      }
    }
  }
}
