import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import { AuthService } from '@app/auth/auth.service';

@Component({
  selector: 'app-visualisation-service-overview',
  templateUrl: './visualisation-service-overview.component.html',
  styleUrls: ['./visualisation-service-overview.component.css']
})
export class VisualisationServiceOverviewComponent implements OnInit {
  @Output() setSubmitDisabled = new EventEmitter<boolean>();

  subOrderDetails: any;
  allowedFileExt = ['jpg', 'jpeg', 'png', 'JPG']; // allowed file extensions for retouching photos

  constructor(
    public orderService: OrdersService,
    public auth: AuthService) {
    this.subOrderDetails = this.orderService.currentSuborder;
  }

  ngOnInit() {
  }

  /**
   * controls disabling of attachment deletion
   */
  deleteAttachmentDisabled() {
    if (this.subOrderDetails.status === 'uploaded' || this.subOrderDetails.status === 'completed'
      || this.auth.role === 'Kunde'
    ) {
      return true;
    }
    return false;
  }

  /**
   * controls disabling of attachment upload
   */
  isUploadDisabled() {
    if (this.subOrderDetails.status === 'uploaded' || this.subOrderDetails.status === 'completed'
      || this.auth.role === 'Kunde'
    ) {
      return true;
    }
    return false;
  }

  setSubmitDisabledHandler(value: boolean) {
    this.setSubmitDisabled.emit(value);
  }
}
