import { Component, OnInit } from '@angular/core';
import { UsersService } from '../_services/users.service';
import { AuthService } from '@app/auth/auth.service';
import { LoaderService } from '@app/_services/loader.service';
import { UploadService } from '@app/_services/upload.service';
import { AngularFirestore } from '@angular/fire/firestore';
import { FormBuilder, FormGroup } from '@angular/forms';
import { tap } from 'rxjs/operators';
import { CloseScrollStrategy } from '@angular/cdk/overlay';
import { OrdersService } from '@app/_services/orders.service';
import { Router } from '@angular/router';

@Component({
  selector: 'app-users',
  templateUrl: './users.component.html',
  styleUrls: ['./users.component.css']
})
export class UsersComponent implements OnInit {

  serviceForm: FormGroup;
  private currentUserData: any;
  public state = 'default';
  public success = false;
  user$;
  user: any;
  configuration;
  configurationDocumentId = 'rsidBwFzO3Kp356emm9N';
  showSuccessMessage = false;
  successMessage = '';

  constructor(private fb: FormBuilder,
    private afs: AngularFirestore,
    public auth: AuthService,
    public loader: LoaderService,
    private us: UsersService,
    private os: OrdersService,
    private router: Router
  ) {
    // Set the validations for the serviceProvider form
    this.serviceForm = this.fb.group({
      floorplanService: [''],
      retouchingService: [''],
      vstagingService: ['']
    });
  }

  ngOnInit() {
    const user = JSON.parse(sessionStorage.getItem('user'));
    const uid: string = user.uid;
    this.user = uid; // UID;
    this.preloadData();
  }

  ngOnDestroy() {
    this.configuration.unsubscribe(); // Missing or insufficient permissions Error, Memory Leak fix
  }

  /**
   * Opens user's detail view
   */
  openDetailPage(uidParam: string) {
    this.currentUserData = this.us.getUserDetails(uidParam);
    this.state = 'detail-view';
  }
  /**
   * Sets the pilot as verified (called by admin) after he(the pilot/service provider) uploads the required documents.
   */
  async setVerified(uidParam: string) {
    await this.us.setVerified(uidParam);
    this.success = true;
    setTimeout(() => {
      this.success = false;
    },
      2000);
  }

  /**
   * Sets the emailVerfied as true in user document (called by admin)
   */
  setEmailVerified(uid: string) {
    this.us.updateUser(uid, { emailVerified: true }).then(() => {
      console.log('email verfied successfully: ');
      this.successMessage = 'E-Mail erfolgreich verifiziert!';
      this.showSuccessMessage = true;
    }).catch(function (error) {
      alert('Unable to verify email!');
      console.log('unable to verify email: ', error);
    });
    setTimeout(() => {
      this.showSuccessMessage = false;
      this.successMessage = '';
    },
      2000);
  }
  /**
   *  Admin can delete the uploaded documents by users (service providers)
   */
  deleteDoc(doc: string, uid: string) {
    this.us.deleteDoc(doc, uid);
  }

  get floorplanService() {
    return this.serviceForm.get('floorplanService');
  }
  get retouchingService() {
    return this.serviceForm.get('retouchingService');
  }
  get vstagingService() {
    return this.serviceForm.get('vstagingService');
  }

  /**
   * Submite handler for service provider form. Updates the user document in firebase
   */
  async submitHandler() {
    const colRef = this.afs.collection('users');
    const config = this.afs.collection('configuration');
    const formValue = this.serviceForm.value;
    this.serviceForm.markAsPristine();
    if (formValue.floorplanService !== this.currentUserData.floorplanService) {
      let updateServiceProviderError = false;
      try {
        await colRef.doc(formValue.floorplanService).update({
          floorplan: true
        });
      } catch (err) {
        console.error(err);
        updateServiceProviderError = true;
      }
      if (!updateServiceProviderError) {
        try {
          await colRef.doc(this.currentUserData.floorplanService).update({
            floorplan: false
          });
          await config.doc(this.configurationDocumentId).update({
            floorplanService: formValue.floorplanService
          });
          this.os.changeAssignment('floorplan', formValue.floorplanService);
        } catch (err) {
          console.error(err);
        }
      } else {
        // ToDo: show user error: id not found in database, nothing updated
      }
    }
    if (formValue.retouchingService !== this.currentUserData.retouchingService) {
      let updateServiceProviderError = false;
      try {
        await colRef.doc(formValue.retouchingService).update({
          retouching: true
        });
      } catch (err) {
        console.error(err);
        updateServiceProviderError = true;
      }
      if (!updateServiceProviderError) {
        try {
          await colRef.doc(this.currentUserData.retouchingService).update({
            retouching: false
          });
          await config.doc(this.configurationDocumentId).update({
            retouchingService: formValue.retouchingService
          });
          this.os.changeAssignment('retouching', formValue.retouchingService);
        } catch (err) {
          console.error(err);
        }
      } else {
        // ToDo: show user error: id not found in database, nothing updated
      }
    }
    if (formValue.vstagingService !== this.currentUserData.vstagingService) {
      let updateServiceProviderError = false;
      try {
        await colRef.doc(formValue.vstagingService).update({
          vstaging: true
        });
      } catch (err) {
        console.error(err);
        updateServiceProviderError = true;
      }
      if (!updateServiceProviderError) {
        try {
          await colRef.doc(this.currentUserData.vstagingService).update({
            vstaging: false
          });
          await config.doc(this.configurationDocumentId).update({
            vstagingService: formValue.vstagingService
          });
          this.os.changeAssignment('vstaging', formValue.vstagingService);
        } catch (err) {
          console.error(err);
        }
      } else {
        // ToDo: show user error: id not found in database, nothing updated
      }
    }
  }
  /**
   * Preload data for user configuration
   */
  async preloadData() {
    this.configuration = await this.afs.doc(`configuration/${this.configurationDocumentId}`).valueChanges().pipe(
      tap(data => {
        this.serviceForm.patchValue(data);
        this.currentUserData = data;
      })
    )
      .subscribe();
  }

  createNewUser(): void {
    this.auth.adminFlag = true;
    this.router.navigate(['/register']);
  }
}
