import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { AuthService } from '../auth/auth.service';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { LoaderService } from '@app/_services/loader.service';
import { Router } from '@angular/router';
import { UsersService } from '@app/_services/users.service';
import { passwordValidator, repeatPasswordValidator } from '@app/shared/form.validators';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit, AfterViewInit {

  lowerCase = false;
  upperCase = false;
  digit = false;
  length = false;
  passwordHelp = false;
  @ViewChild('newPwd') newPwd;

  public conditionsAccepted = false;
  public registerForm: FormGroup;
  public passwordError = false;
  public roleError = false;
  adminFlag: boolean;
  popoverPlacement: string;
  constructor(public auth: AuthService, private fb: FormBuilder, public loader: LoaderService, private router: Router) {
    this.registerForm = this.fb.group({
      anrede: ['Anrede auswhlen', [
        Validators.required,
        Validators.pattern('[a-zA-Z]*')
      ]],
      firstName: ['', [
        Validators.required,
        Validators.pattern('^([A-Z-][a-zA-Z -]*[a-zA-Z]$)')
      ]],
      lastName: ['', [
        Validators.required,
        Validators.pattern('^([A-Z-][a-zA-Z -]*[a-zA-Z]$)')
      ]],
      email: ['', [
        Validators.required,
        Validators.pattern('^[a-zA-Z0-9.!#$%&*+/=?^_`{|}~-]+@[a-zA-Z0-9-]+(?:\.[a-zA-Z0-9-]+)*$')
      ]],
      password1: ['', [
        Validators.required,
        passwordValidator()
      ]],
      password2: ['', [
        Validators.required,
        passwordValidator()
      ]],
      role: ['Nutzerrolle whlen', [
        Validators.required,
        Validators.pattern('[a-zA-Z]*')
      ]],
      conditions: [false, [
        Validators.required
      ]]
    });
  }

  ngOnInit() {
    this.adminFlag = this.auth.adminFlag;
    this.auth.checkAuthState().then(result => {
      if (result && !this.adminFlag) {
        this.router.navigate(['/users']);
      }
    });
    if (window.matchMedia(`(min-width: 960px)`).matches) {
      this.popoverPlacement = 'right';
    } else {
      this.popoverPlacement = 'top';
    }
  }

  ngAfterViewInit() {
    const newPwd = this.newPwd.nativeElement;
    this.password2.setValidators([Validators.required, passwordValidator(), repeatPasswordValidator(newPwd)]);
  }

  get anrede() {
    return this.registerForm.get('anrede');
  }
  get firstName() {
    return this.registerForm.get('firstName');
  }
  get lastName() {
    return this.registerForm.get('lastName');
  }
  get email() {
    return this.registerForm.get('email');
  }
  get password1() {
    return this.registerForm.get('password1');
  }
  get password2() {
    return this.registerForm.get('password2');
  }
  get role() {
    return this.registerForm.get('role');
  }

  get conditions() {
    return this.registerForm.get('conditions');
  }

  async submitHandler() {
    const formValue = this.registerForm.value;
    Object.keys(this.registerForm.controls).forEach(control => {
      this.registerForm.controls[control].updateValueAndValidity();
      this.registerForm.controls[control].markAsTouched();
    });
    console.log(this.registerForm);
    if (!this.registerForm.valid || !this.conditionsAccepted) {
      return;
    }
    try {
      if (formValue.password1 === formValue.password2) {
        this.roleError = false;
        this.passwordError = false;
        if (!this.adminFlag) {
          this.auth.registering = true;
        }
        await this.auth.register(formValue).then(result => {
          if (!this.adminFlag) {
            this.loader.getDataAfterAuthenticated();
          } else {
            this.router.navigate(['/users']);
          }
        },
          reject => {
            console.log('Do Nothing');
          });
      } else {
        console.log(formValue.password1);
        console.log(formValue.password2);

        this.passwordError = true;
        console.log('Passwort Error');
      }
    } catch (err) {
      window.alert(err);
    }
  }

  conditionsChecked() {
    this.conditionsAccepted = !this.conditionsAccepted;
  }
}
