import { Component, OnInit, Input, Output, EventEmitter } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import { AuthService } from '@app/auth/auth.service';
import * as _ from 'underscore';

@Component({
  selector: 'app-retouching-suborder-overview',
  templateUrl: './retouching-suborder-overview.component.html',
  styleUrls: ['./retouching-suborder-overview.component.css']
})
export class RetouchingSuborderOverviewComponent implements OnInit {

  @Output() setSubmitDisabled = new EventEmitter<boolean>();

  allowedFileExt = ['jpg', 'jpeg', 'png', 'JPG']; // allowed file extensions for retouching photos
  constructor(
    public os: OrdersService,
    public auth: AuthService
  ) { }

  ngOnInit() {
  }

  /**
   * controls disabling of attachment deletion
   */
  deleteAttachmentDisabled() {
    if (this.os.currentSuborder.status === 'uploaded' || this.os.currentSuborder.status === 'completed'
      || this.auth.role === 'Kunde'
    ) {
      return true;
    }
    return false;
  }

  /**
   * controls disabling of attachment upload
   */
  isUploadDisabled() {
    if (this.os.currentSuborder.status === 'uploaded' || this.os.currentSuborder.status === 'completed'
      || this.auth.role === 'Kunde'
    ) {
      return true;
    }
    return false;
  }

  setSubmitDisabledHandler(value: boolean) {
    this.setSubmitDisabled.emit(value);
  }

}
