  ngOnInit() {
    let self = this;
    self.state = '1';
    this.os.getAllServices().subscribe((services: any[]) => {
      let PACKAGES = {};
      self.packageList = [];
      services.forEach((service: any) => {
        PACKAGES[service.id] = service.packages;
        self.packageList = self.packageList.concat(service.packages);
      });
      self.packages = PACKAGES;
      self.availablePackages = self.fetchAvailableServices('', false);
      const user = JSON.parse(sessionStorage.getItem('user'));
      const uid: string = user.uid;
      self.user = uid; // UID;
      self.preloadData();
      self.packages.dronemedia = self.packages.dronemedia.filter(pckg => !pckg.disabled);
      self.packages.floorplans = self.packages.floorplans.filter(pckg => !pckg.disabled);
      self.packages.retouching = self.packages.retouching.filter(pckg => !pckg.disabled);
      self.packages.vstaging = self.packages.vstaging.filter(pckg => !pckg.disabled);
      self.packages.visualisation = self.packages.visualisation.filter(pckg => !pckg.disabled);
      self.packages.hdphotos = self.packages.hdphotos.filter(pckg => !pckg.disabled);
      if (self.userService.cartItems) {
        self.cartItems = self.userService.cartItems;
      }
      if (self.userService.selectedServices) {
        self.selectedServices = self.userService.selectedServices;
      }
    });
  }

