  /**
   * Submit handler for booking a service for a realestate.
   * Also updates the address in user profile if the billing address is updated in during booking a service.
   */
  async submitHandler() {
    console.log('Check');
    const colRef = this.afs.collection('users');
    this.profileForm.value.profileCompleted = true;
    const formValue = this.profileForm.value;
    colRef.doc(this.user).update(formValue);
    this.bookOrder();
  }

  async createNewRealestate() {
    this.realestateservice.add = false;
    const colRef = this.afs.collection('realestates');
    const formValue = this.realestateForm.value;
    try {
      await colRef.add(formValue).then(docRef => {
        const docId = docRef.id;
        colRef.doc(docId).update({
          id: docId,
          ownerID: this.user,
          createdOn: new Date(),
        });
        this.realestateForm.reset();
        this.realestateForm.markAsPristine();
      });
    } catch (err) {
      console.error(err);
    }
  }

