  goNext(): void {
    if (this.selectedPackages && this.selectedPackages.length <= 0) {
      this.gs.showNotification('Bitte whlen Sie ein Paket aus', 'danger');
      return;
    }
    var errorMsg = '';
    var canContinue: boolean = true;
    this.total_price_sum = 0;
    this.package_price_list = [];
    // add price for dronemedia package
    if (!_.isEmpty(this.selectedDroneMedia) && canContinue) {
      this.priceCalculation(this.selectedDroneMedia);
    }
    this.selectedFloorplans.forEach(plan => {
      if (plan.form.invalid) {
        canContinue = false;
        plan.validate = !plan.validate;
      } else if (canContinue) {
        this.priceCalculation(plan);
      }
    });
    // retouching form validation
    this.selectedRetouching.forEach(retouching => {
      if (retouching.form.invalid) {
        canContinue = false;
        retouching.validate = !retouching.validate;
      } else if (canContinue) {
        this.priceCalculation(retouching);
      }
    });
    // virtual staging form valdiation
    this.selectedVStaging.forEach(staging => {
      if (staging.form.invalid) {
        canContinue = false;
        staging.validate = !staging.validate;
      } else if (canContinue) {
        this.priceCalculation(staging);
      }
    });
    // // 3D Visualisation form valdiation
    this.selected3dVisualisation.forEach(vis => {
      if (!vis.total_price || vis.total_price === '0,00') {
        canContinue = false;
        vis.validate = !vis.validate;
        errorMsg = 'Bitte geben Sie alle bentigten Informationen an';
      } else if (canContinue) {
        this.priceCalculation(vis);
      }
    });
    this.selectedHDPhotos.forEach(hdPhotos => {
      if (hdPhotos.form.invalid) {
        canContinue = false;
        hdPhotos.validate = !hdPhotos.validate;
        errorMsg = 'Bitte geben Sie alle bentigten Informationen an';
      } else if (canContinue) {
        this.priceCalculation(hdPhotos);
      }
    });
    // expose form valdiation
    this.selectedExposes.forEach(expose => {
      if (expose.form.invalid) {
        canContinue = false;
        expose.validate = !expose.validate;
      } else if (canContinue) {
        this.priceCalculation(expose);
      }
    });
    if (canContinue) {
      this.state = '5';
      this.verzichtWiderruf = false;
      this.total_price_sum = accounting.formatMoney(this.total_price_sum, '', 2, '.', ',');
    } else {
      if (!errorMsg) {
        this.gs.showNotification('Bitte laden Sie die bentigten Dateien hoch', 'danger');
      } else {
        this.gs.showNotification(errorMsg, 'danger');
      }

    }
  }

  priceCalculation(package_data: any) {
    const total_price = package_data.total_price ? package_data.total_price : package_data.price;
    // add price
    this.total_price_sum += accounting.unformat(total_price, ',');
    this.package_price_list.push({
      package_label: LANGUAGE.app_list_strings.order_type_list[package_data.service_key] + ' - ' + package_data.name,
      total_price: total_price
    });
  }

