import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import { AuthService } from '@app/auth/auth.service';

@Component({
  selector: 'app-virtual-staging-suborder-overview',
  templateUrl: './virtual-staging-suborder-overview.component.html',
  styleUrls: ['./virtual-staging-suborder-overview.component.css']
})
export class VirtualStagingSuborderOverviewComponent implements OnInit {
  allowedFileExt = ['jpg', 'jpeg', 'png', 'JPG']; // allowed file extensions for virtual staging photos
  @Output() setSubmitDisabled = new EventEmitter<boolean>();

  constructor(
    public os: OrdersService,
    public auth: AuthService) { }

  ngOnInit() {
  }
  /**
   * controls disabling of attachment deletion
   */
  deleteAttachmentDisabled() {
    if (this.os.currentSuborder.status === 'uploaded' || this.os.currentSuborder.status === 'completed'
      || this.auth.role === 'Kunde'
    ) {
      return true;
    }
    return false;
  }

  /**
   * controls disabling of attachment upload
   */
  isUploadDisabled() {
    if (this.os.currentSuborder.status === 'uploaded' || this.os.currentSuborder.status === 'completed'
      || this.auth.role === 'Kunde'
    ) {
      return true;
    }
    return false;
  }

  setSubmitDisabledHanlder(value: boolean) {
    this.setSubmitDisabled.emit(value);
  }
}
