import { Component, OnInit } from '@angular/core';
import { RealestateService } from 'src/app/_services/realestate.service';
import { OrdersService } from '@app/_services/orders.service';
import { LoaderService } from '@app/_services/loader.service';
import { UploadService } from '@app/_services/upload.service';
import { Observable } from 'rxjs';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { AngularFirestore } from '@angular/fire/firestore';
import { AuthService } from '@app/auth/auth.service';
import * as firebase from 'firebase/app';
import { AngularFireStorage } from '@angular/fire/storage';

@Component({
  selector: 'app-orderoverview',
  templateUrl: './orderoverview.component.html',
  styleUrls: ['./orderoverview.component.css']
})
export class OrderoverviewComponent implements OnInit {
  createdByPersonData: any;
  createdByKundeData: any;
  state: string;
  currentRealestateData: Observable<any>;
  currentOrderData: any;
  userRef: any;
  success = false;
  photoUploadForm: FormGroup;
  uploading = false;
  error = false;
  suborder_state: string;

  constructor(public realestateservice: RealestateService,
    private os: OrdersService,
    public auth: AuthService,
    private fb: FormBuilder,
    private uploadService: UploadService,
    private afs: AngularFirestore,
    public loader: LoaderService,
    private afStorage: AngularFireStorage, ) {
    this.photoUploadForm = this.fb.group({
      photoLink: ['', [
        Validators.required,
        Validators.pattern('^(http|https|ftp)\://[a-zA-Z0-9\-\.]+\.[a-zA-Z]{2,3}(:[a-zA-Z0-9]*)?/?([a-zA-Z0-9\-\._\?\,\'/\\\+&amp;%\$#\=~])*[^\.\,\)\(\s]$')
      ]]
    });
  }

  ngOnInit() {
    const user = JSON.parse(sessionStorage.getItem('user'));
    this.userRef = user.uid;
    this.state = 'default';
    this.suborder_state = 'list-view';
  }

  get photoLink() {
    return this.photoUploadForm.get('photoLink');
  }

  /**
   *
   * Gets triggered when order detail button is clicked
   * Shows the details of the order and set the component state according to user role
   */
  openDetailPage(orderIdParam: string, ridParam: string, myornotParam: boolean, creatorId?: any) {
    this.currentRealestateData = this.os.getRealestateDetails(ridParam);
    this.currentOrderData = this.os.getOrderDetails(orderIdParam);
    if (creatorId === undefined) {
      // do nothing when creatorId is undefined
    } else {
      this.createdByPersonData = this.os.getCreatorDetails(creatorId);
    }

    if (myornotParam) {
      this.state = 'my-detail-view';
    } else {
      this.state = 'detail-view';
    }
  }


  /**
   * Admin can delete an order. Delete the order document and update the realestate 
   */
  async deleteOrder(oidParam: string, ridParam: string) {
    const confirmed = confirm('Mchten Sie den Auftrag wirklich lschen? Diese Aktion kann nicht rckgngig gemacht werden!');
    if (confirmed) {
      try {
        await this.os.deleteOrder(oidParam, ridParam);
        this.success = true;
        setTimeout(() => {
          this.state = 'default';
          this.success = false;
        },
          600);
      } catch (e) {
        console.error(e);
        this.success = false;
        this.error = true;
      }
    } else {
      alert('Lschvorgang abgebrochen!');
    }
  }

  /**
 * Customer can cancel an order. Status of order and suborders will be changed to 'canceled'
 */
  async cancelOrder(oidParam: string) {
    const confirmed = confirm('Mchten Sie den Auftrag wirklich stornieren? Diese Aktion kann nicht rckgngig gemacht werden!');
    if (confirmed) {
      try {
        await this.os.cancelOrder(oidParam);
        this.success = true;
        setTimeout(() => {
          this.state = 'default';
          this.success = false;
        },
          600);
      } catch (e) {
        console.error(e);
        this.success = false;
        this.error = true;
      }
    } else {
      alert('Stornierung abgebrochen!');
    }
  }

  /**
   * Saves the information that both parties have been notified via E-Mail by an Admin as
   * a property inside the order database entry. This property will later be used to hide the buttons
   * so different Admins or the same Admin will not notify the customer & pilot again.
   */
  async saveNotificationStatusChange(oidParam: string) {
    this.os.changeOrder(oidParam, 'notifcationSent', true);
  }

  /**
   * Gets triggered when service provider completes uploading all the photos and marks the order as complete
   */
  async uploadOrder(oidParam: string) {
    try {
      await this.os.setStatusUploaded(oidParam);
      setTimeout(() => {
        this.state = 'default';
      },
        600);
    } catch (e) {
      console.error(e);
      this.error = true;
    }

  }
  /**
   * Gets triggered when admin finishes the post processing and marks the oder as complete after uploading all the files.
   */
  async completeOrder(oidParam: string, ridParam: string) {
    const confirmed = confirm('Sind Sie sicher, dass Sie den Auftrag abschlieen wollen? Diese Aktion kann nicht rckgngig gemacht werden. Dem Kunde wird der abgeschlossene Auftrag sofort angezeigt.');
    if (confirmed) {
      try {
        await this.os.setStatusCompleted(oidParam, ridParam);
        this.success = true;
        setTimeout(() => {
          this.state = 'default';
          this.success = false;
        },
          600);
      } catch (e) {
        console.error(e);
        this.success = false;
        this.error = true;
      }
    } else {
      alert('Lschvorgang abgebrochen!');
    }
  }


  /**
   * Gets triggered when service provider uploads a photo to an order
   */
  async uploadBild(event, orderid: string, nameParam: string) {
    const filename = event.target.files[0].name;
    this.uploading = true;
    // Upload the file to firestore storage
    await this.uploadService.uploadToOrder(event, orderid, filename);
    const downloadURL = this.uploadService.downloadURL;
    // update the uploaded file url in orders document
    await this.afs.collection('orders').doc(orderid).update({
      [nameParam]: downloadURL
    });
    this.uploading = false;
    this.success = true;
    setTimeout(() => {
      this.success = false;
    },
      2000);
  }

  /**
 * Gets triggered when administrator uploads a file to the order (e.g invoice)
 * Uploads the file to firebase storage and updates the url in order document
 */
  async uploadToOrder(event, orderId: string) {
    this.uploading = true;
    await this.uploadService.uploadToOrder(event, orderId, event.target.files[0].name);
    const downloadURL = this.uploadService.downloadURL;
    await this.afs.collection('orders').doc(orderId).update({
      customerInvoiceUrl: downloadURL,
      customerInvoiceName: event.target.files[0].name
    });
    this.uploading = false;
  }

  /**
   * Delete a document from the order
   */
  deleteFile(orderId: string, urlField: string, fileName: string) {
    const confirmed = confirm('Sind Sie sicher, dass Sie dieses Dokument lschen mchten?');
    if (confirmed) {
      if (urlField === 'customerInvoiceUrl') {
        // delete file reference from order document
        return this.afs.collection('orders').doc(orderId).update({
          customerInvoiceUrl: firebase.firestore.FieldValue.delete(),
          customerInvoiceName: firebase.firestore.FieldValue.delete()
        }).then(() => {
          // delete file from storage
          this.afStorage.ref(`orders/${orderId}/${fileName}`).delete();
        })
          .catch(error => console.log(error));
      }
    }
  }

}
