import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Package } from '@app/models/package';
import { FormBuilder, Validators } from '@angular/forms';

@Component({
  selector: 'app-expose-service',
  templateUrl: './expose-service.component.html',
  styleUrls: ['./expose-service.component.css']
})
export class ExposeServiceComponent implements OnInit {
  @Output() onFormChange = new EventEmitter();
  @Input("package") package: Package;
  exposeForm: any;

  constructor(private fb: FormBuilder) {
    this.exposeForm = this.fb.group({
      description: ['']
    });
  }

  ngOnInit() {
    this.package["validate"] = true;
    this.exposeForm.valueChanges.subscribe(() => {
      let formValues = {};
      formValues['description'] = this.exposeForm.value.description;
      formValues['packageNum'] = this.package['package_key'];
      this.onFormChange.emit(formValues);
    });
    this.package.form = this.exposeForm;
  }

  ngAfterViewInit() {

  }

  onChange(): void {
    this.package.form = this.exposeForm;
  }

  get description() {
    return this.exposeForm.get('description');
  }

  calculatePrice() {
    return this.package.price;
  }

}
