import { Component, OnInit, Output, EventEmitter } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import { AuthService } from '@app/auth/auth.service';

@Component({
  selector: 'app-floorplan-suborder-overview',
  templateUrl: './floorplan-suborder-overview.component.html',
  styleUrls: ['./floorplan-suborder-overview.component.css']
})
export class FloorplanSuborderOverviewComponent implements OnInit {
  subOrderDetails;
  public allowedFileExt: string[] = ['pdf', 'jpeg', 'png', 'jpg', 'JPG'];
  @Output() setSubmitDisabled = new EventEmitter<boolean>();

  constructor(
    public orderService: OrdersService,
    public auth: AuthService
  ) {
    this.subOrderDetails = this.orderService.currentSuborder;
  }

  ngOnInit() {
  }

  setSubmitDisabledHanlder(value: boolean) {
    this.setSubmitDisabled.emit(value);
  }

  /**
   * controls disabling of attachment deletion
   */
  deleteAttachmentDisabled() {
    if (
      this.orderService.currentSuborder.status === 'uploaded' ||
      this.orderService.currentSuborder.status === 'completed') {
      return true;
    }
    return false;
  }

    /**
   * controls disabling of attachment upload
   */
  isUploadDisabled() {
    if (this.orderService.currentSuborder.status === 'uploaded' ||
    this.orderService.currentSuborder.status === 'completed') {
      return true;
    }
    return false;
  }

}
