import { Component, OnInit, Output, Input, EventEmitter } from '@angular/core';
import { Package } from '@app/models/package';
import { FormGroup, FormBuilder, Validators } from '@angular/forms';
import * as accounting from 'accounting';

@Component({
  selector: 'app-visualisation-service',
  templateUrl: './visualisation-service.component.html',
  styleUrls: ['./visualisation-service.component.css']
})
export class VisualisationServiceComponent implements OnInit {
  @Output() onFormChange = new EventEmitter();
  @Input("package") package: Package;

  visualisationForm: FormGroup;

  active1: boolean = false;
  outputFormat: number = 0;
  quality: number = 0;
  length: number = 0;
  apartments: number = 0;
  price: number = 0;
  total_price = '0,00';
  additionalPrice: number = 100;
  priceMatrix: number[][];
  lengthMatrix: number[] = [30, 40, 50];
  apartmentsMatrix: number[] = [20, 25, 50, 100];

  constructor(private fb: FormBuilder) {
    this.visualisationForm = this.fb.group({
      description: [''],
      numberPhotos: [1, [Validators.min(1)]]
    });
  }

  ngOnInit() {
    this.package.form = this.visualisationForm;
    this.visualisationForm.valueChanges.subscribe(() => {
      let formValues = {};
      formValues['description'] = this.visualisationForm.value.description;
      formValues['numberPhotos'] = this.visualisationForm.value.numberPhotos;
      formValues['packageNum'] = this.package['package_key'];
      this.onFormChange.emit(formValues);
    });
    if (this.package.name === 'Innenvisualisierung') {
      this.priceMatrix = [[210, 290, 450, 800], // Photo
      [290, 390, 700, 900], // 360-degree
      [40, 60, 0, 0]]; // video
    } else {
      this.priceMatrix = [[250, 300, 650, 950], // Photo
      [0, 0, 0, 0], // 360-degree
      [0, 0, 0, 0]]; // video
    }
  }

  onChange(): void {
    this.package['outputFormat'] = this.outputFormat;
    this.package['quality'] = this.quality;
    this.package['length'] = this.length;
    this.package['apartments'] = this.apartments;
    this.package['total_price'] = this.total_price;
    this.package['numPhotos'] = this.visualisationForm.value.numberPhotos;
    this.package['description'] = this.visualisationForm.value.description;
    this.package.form = this.visualisationForm;
  }

  get description() {
    return this.visualisationForm.get('description');
  }

  get numberPhotos() {
    return this.visualisationForm.get('numberPhotos');
  }

  packageSelectHandler(service, number, event) {
    switch (service) {
      case 'outputFormat':
        if (this.package.name === 'Innenvisualisierung') {
          if (this.outputFormat === 3 || number === 3) {
            this.quality = 0;
            this.length = 0;
            this.visualisationForm.controls['numberPhotos'].setValue(1);
          }
          if (this.outputFormat === number) {
            this.outputFormat = 0;
          } else {
            this.outputFormat = number;
          }
        } else {
          if (this.outputFormat === number || number > 1) {
            this.outputFormat = 0;
          } else {
            this.outputFormat = number;
          }
        }
        break;
      case 'quality':
        if (this.quality === number) {
          this.quality = 0;
        } else {
          this.quality = number;
        }
        break;
      case 'length':
        if (this.length === number) {
          this.length = 0;
        } else {
          this.length = number;
        }
        break;
      case 'apartments':
        if (this.apartments === number) {
          this.apartments = 0;
        } else {
          this.apartments = number;
        }
        break;
    }
    this.updatePrice();
  }

  updatePrice() {
    if (!this.outputFormat || !this.quality) {
      this.price = 0;
    } else {
      this.price = this.priceMatrix[this.outputFormat - 1][this.quality - 1];
      if (this.package.name === 'Innenvisualisierung') {
        if (this.outputFormat === 3) {
          if (this.length) {
            this.price *= this.lengthMatrix[this.length - 1];
          } else {
            this.price = 0;
          }
        }
      } else {
        if (this.apartments) {
          this.price += (this.apartments - 1) * this.apartmentsMatrix[this.quality - 1];
        } else {
          this.price = 0;
        }
      }
    }
    this.calculateSum();
  }

  calculateSum() {
    if (this.outputFormat === 3) {
      if (this.visualisationForm.get('numberPhotos').value > 3) {
        this.total_price = accounting.formatMoney(this.price + (this.visualisationForm.get('numberPhotos').value - 3) * this.additionalPrice, '', 2, '.', ',');
      } else {
        this.total_price = accounting.formatMoney(this.price, '', 2, '.', ',');
      }
    } else {
      this.total_price = accounting.formatMoney(this.price * this.visualisationForm.get('numberPhotos').value, '', 2, '.', ',');
    }
    this.onChange();
  }
}
