import { Injectable, NgZone, EventEmitter } from '@angular/core';
import { AngularFireAuth } from '@angular/fire/auth';
import { AngularFirestore, AngularFirestoreDocument } from '@angular/fire/firestore';
import { Router } from '@angular/router';
import { Observable } from 'rxjs';
import { first } from 'rxjs/operators';
import { NgxSmartModalService } from 'ngx-smart-modal';
import * as _ from 'underscore';
import { AngularFireFunctions } from '@angular/fire/functions';
import { GlobalService } from '@app/_services/global.service';
import { reject } from 'q';
import * as firebase from 'firebase/app';

@Injectable({
  providedIn: 'root'
})

export class AuthService {

  public sessionEnded = false;
  userData: any; // Save logged in user data
  role: any;
  emailSub: any;
  userSub: any;
  userAuthState: any;
  checkStateSub: any;
  emailVerificationFailed = false;
  floorplan: any;
  dronepilot: any;
  retouching: any;
  public emailVerified = false;
  public profileCompleted = true;
  public myUserObservable: any;
  public authorised = false;
  public checkingEmail = false;
  public registering = false;
  uid = null;
  uSub: any;
  public adminFlag: boolean;
  private createUserCallable: any;
  public showLoader: EventEmitter<boolean> = new EventEmitter<boolean>();

  constructor(
    public afs: AngularFirestore,
    public afAuth: AngularFireAuth,
    public router: Router,
    public ngZone: NgZone,
    public ngxSmartModalService: NgxSmartModalService,
    private fns: AngularFireFunctions,
    private gs: GlobalService
  ) {
    this.createUserCallable = this.fns.httpsCallable('createUser');
  }

  checkAuthState(): Promise<boolean> {
    /* Since the router calls  the CanActivate() method on almost every route which in turn calls this,
    we would have many superflous database calls on every route-change, thus we set authorised = true;
    after the first AuthCheck, so we can catch every call after the successful-login with a simple if-statement.
    When the user logs out, this.authorised has to be set to false, because the page is not reloaded.
    */
    if (this.authorised === true) {
      // Not needed to ask Firebase
      return new Promise<boolean>(resolve => {
        console.log('[AUTH]: Logged-in! [*]');
        resolve(true);
      });
    } else {
      return new Promise<boolean>(resolve => {
        this.afAuth.authState.subscribe(async user => {
          if (user) {
            this.authorised = true;
            const currUser = JSON.parse(sessionStorage.getItem('user'));
            if (!this.uid && currUser && currUser.uid) {
              this.uid = currUser.uid;
            }
            if (this.uSub) {
              this.uSub.unsubscribe();
            }
            this.uSub = this.afs.collection('users').doc(`${this.uid}`).valueChanges().subscribe(data => {
              if (data) {
                this.myUserObservable = data;
                this.emailVerified = this.myUserObservable.emailVerified || user.emailVerified;
              } else {
                this.emailVerified = user.emailVerified;
              }
              console.log('[AUTH]: AuthState: Logged-in!');
              // close email verify popup
              _.each(this.ngxSmartModalService.getOpenedModals(), function (obj) {
                if (obj.id === 'emailVerificationModal') {
                  if (this.emailVerified) {
                    this.ngxSmartModalService.close('emailVerificationModal');
                  }
                }
              }, this);
              resolve(true);
            });
            //this.emailVerified = user.emailVerified;
            //resolve(true);
          } else {
            this.emailVerified = false;
            console.log('[AUTH]: AuthState: Not logged-in!');
            sessionStorage.setItem('user', null);
            resolve(false);
          }
        });
      });
    }
  }

  /* Method used by the Loader-Service inside the getDataAfterAuthenticated(),
  returns a promise, to ensure that we don't try to load data before the user is initialisied and
  we can access his role and won't work with an undefined value for it. */
  initializeUser(): Promise<boolean> {
    return new Promise<boolean>(resolve => {
      this.userAuthState = this.afAuth.authState.subscribe(async user => {
        if (user) {
          console.log('[3]: Initializing User-Values!');
          this.userData = user;
          this.authorised = true;
          sessionStorage.setItem('user', JSON.stringify(this.userData));
          this.userSub = this.getCustomUserDoc().valueChanges().subscribe(data => {
            this.myUserObservable = data;
          });
          this.getCustomUserData().then(result => {
            if (result === true) {
              resolve(true);
            } else {
              //
              window.alert('Fehler 18-5: Bitte kontaktieren Sie den Support!');
            }
          });
        } else {
          console.log('[3 & 4]: AuthState: Not logged-in!');
          sessionStorage.setItem('user', null);
          this.authorised = false;
          resolve(false);
        }
        this.userAuthState.unsubscribe();
      });
    });
  }
  /**
   * Use the angularfire auth service to check if the user email is verified. If it is verified, we updated the emailVerified bit to true
   * in user document in the user collection.
   */
  verifyEmail() {
    return new Promise<boolean>(resolve => {
      this.checkingEmail = true;
      setTimeout(() => {
        /* We need to update the user to see if the E-Mail has been verified. The Auth User Object is not
           updated automatically */
        this.afAuth.auth.currentUser.reload()
          .then((result => {
            this.emailSub = this.afAuth.authState.subscribe(async user => {
              // if the email is verified already in user document
              if (this.myUserObservable && this.myUserObservable.emailVerified) {
                console.log('[EMAIL]: Verified in user document!');
                this.emailVerified = true;
                this.emailSub.unsubscribe();
                this.ngxSmartModalService.close('emailVerificationModal');
                resolve(true);
              } else if (user.emailVerified === true) {
                console.log('[EMAIL]: Verified!');
                this.emailVerified = true;
                this.afs.collection('users').doc(user.uid).update({
                  emailVerified: true
                }).then(success => {
                  this.emailSub.unsubscribe();
                  this.ngxSmartModalService.close('emailVerificationModal');
                  resolve(true);
                });
              } else {
                console.warn('[EMAIL]: Not verified!');
                this.emailVerificationFailed = true;
                resolve(false);
                this.emailSub.unsubscribe();
                setTimeout(() => {
                  this.emailVerificationFailed = false;
                }, 3500);
              }
            });
          }));
        this.checkingEmail = false;
      }, 1000); // cosmetic delay
    });
  }

  // Getting the the user role, so it can be used by the loader's process to only load specific data
  getCustomUserData() {
    return new Promise<boolean>(resolve => {
      this.getCustomUserDoc().get().toPromise().then(result => {
        console.log('[4]: All values assigned');
        this.role = result.data().role;
        this.emailVerified = result.data().emailVerified;
        this.profileCompleted = result.data().profileCompleted;
        this.floorplan = result.data().floorplan;
        this.dronepilot = result.data().dronepilot;
        this.retouching = result.data().retouching;
        resolve(true);
      });
    });
  }

  // Login with email & password
  login(email, password) {
    return this.afAuth.auth.signInWithEmailAndPassword(email, password)
      .then((result) => {
        this.ngZone.run(() => {
          this.router.navigate(['/home']);
        });
      }).catch((error) => {
        window.alert(error.message);
      });
  }

  // Register with email & password
  register(rFV) {
    return new Promise<boolean>(resolve => {
      try {
        if (!this.adminFlag) {
          this.afAuth.auth.createUserWithEmailAndPassword(rFV.email, rFV.password1)
            .then((result) => {
              this.SendVerificationMail();
              this.uid = result.user.uid;
              this.SetUserData(result.user, rFV)
                .then((userdataset) => {
                  this.registering = false;
                  this.router.navigate(['/home']);
                  resolve();
                });
            });
        } else {
          this.showLoader.emit(true);
          this.createUserCallable({
            'email': rFV.email,
            'password': rFV.password1
          }).subscribe(response => {
            this.showLoader.emit(false);
            if (!response.message) {
              this.SetUserData(response, rFV)
                .then((userdataset) => {
                  this.gs.showNotification('User created successfully', 'success');
                  resolve();
                });
            } else {
              this.gs.showNotification(response.message, 'danger');
              reject();
            }
          });
        }
      } catch (error) {
        window.alert(error.message);
      }
    });
  }

  // Send Verfication-Email
  SendVerificationMail() {
    return this.afAuth.auth.currentUser.sendEmailVerification();
  }

  // Reset Forggot password
  forgot(passwordResetEmail) {
    return this.afAuth.auth.sendPasswordResetEmail(passwordResetEmail)
      .then(() => {
        window.alert('E-Mail wurde gesendet. berprfen Sie ihr E-Mail Postfach!');
      }).catch((error) => {
        window.alert(error);
      });
  }

  // Just used by the Sidebar and for css classes, not needed to run any database checks.
  get isLoggedIn(): boolean {
    const user = JSON.parse(sessionStorage.getItem('user'));
    return (user !== null) ? true : false;
  }

  /* Setting up user data when sign in with username/password,
  sign up with username/password and sign in with social auth
  provider in Firestore database using AngularFirestore + AngularFirestoreDocument service */
  SetUserData(user, rFV) {
    const userRef: AngularFirestoreDocument<any> = this.afs.doc(`users/${user.uid}`);
    const userData = {
      uid: user.uid,
      email: user.email,
      emailVerified: user.emailVerified,
      role: rFV.role,
      anrede: rFV.anrede,
      firstName: rFV.firstName,
      lastName: rFV.lastName,
      registeredOn: new Date(),
      pilotVerified: false,
      verificationNeeded: false,
      profileCompleted: false
    };
    return userRef.set(userData, {
      merge: true
    });
  }

  // Logging the user out
  logout() {
    this.userSub && this.userSub.unsubscribe();
    this.uSub && this.uSub.unsubscribe();
    this.authorised = false;
    this.adminFlag = false;
    return this.afAuth.auth.signOut()
      .then((result) => {
        this.router.navigate(['/logout']);
      }).catch((error) => {
        console.log(error.message);
      });
  }

  // Getting the Document
  getCustomUserDoc() {
    const user = JSON.parse(sessionStorage.getItem('user'));
    const uid = user.uid;
    return this.afs.collection('users').doc(`${uid}`);
  }

  reAuthenticateUser(password: string) {
    const credentials = firebase.auth.EmailAuthProvider.credential(this.userData.email, password);
    return this.afAuth.auth.currentUser.reauthenticateAndRetrieveDataWithCredential(credentials);
  }

  changePassword(password){
    return this.afAuth.auth.currentUser.updatePassword(password);
  }
}
