import { Component, OnInit, ViewChild, AfterViewInit } from '@angular/core';
import { FormGroup, FormControl, Validators } from '@angular/forms';
import { passwordValidator, repeatPasswordValidator, noRepeatPasswordValidator } from '@app/shared/form.validators';
import { AuthService } from '@app/auth/auth.service';
import { GlobalService } from '@app/_services/global.service';
import { LoaderService } from '@app/_services/loader.service';

@Component({
  selector: 'app-change-password',
  templateUrl: './change-password.component.html',
  styleUrls: ['./change-password.component.css']
})
export class ChangePasswordComponent implements OnInit, AfterViewInit {

  changePasswordForm: FormGroup;
  @ViewChild('currentPwd') currentPwd;
  @ViewChild('newPwd') newPwd;

  constructor(
    private authService: AuthService,
    private gs: GlobalService,
    private loader: LoaderService
  ) {
    this.changePasswordForm = new FormGroup({
      'currentPassword': new FormControl('', [
        Validators.required
      ]),
      'newPassword1': new FormControl('', [Validators.required, passwordValidator()]),
      'newPassword2': new FormControl('', [Validators.required, passwordValidator()])
    });
  }

  ngOnInit() { }

  ngAfterViewInit() {
    const currentPwd = this.currentPwd.nativeElement;
    const newPwd = this.newPwd.nativeElement;
    this.newPassword1.setValidators([Validators.required, passwordValidator(), noRepeatPasswordValidator(currentPwd)]);
    this.newPassword2.setValidators([Validators.required, passwordValidator(), repeatPasswordValidator(newPwd)]);
  }

  get currentPassword() {
    return this.changePasswordForm.get('currentPassword');
  }

  get newPassword1() {
    return this.changePasswordForm.get('newPassword1');
  }

  get newPassword2() {
    return this.changePasswordForm.get('newPassword2');
  }

  changePassword(): void {
    Object.keys(this.changePasswordForm.controls).forEach(control => {
      this.changePasswordForm.controls[control].updateValueAndValidity();
      this.changePasswordForm.controls[control].markAsTouched();
    });
    if (this.changePasswordForm.valid) {
      this.authService.reAuthenticateUser(this.currentPassword.value).then(
        success => {
          this.authService.changePassword(this.newPassword1.value).then(
            success => {
              this.gs.showNotification('Password has been changed successfully. Please login with new credentials.', 'success');
              this.loader.logout();
            },
            error => {
              this.gs.showNotification('Password could not be changed. Please try again later.', 'danger');
            }
          );
        },
        error => {
          this.gs.showNotification('Incorrect current password!', 'danger');
        }
      );
    }
  }

  validateOtherFields(controls: string[]): void{
    controls.forEach(control => {
      if(this.changePasswordForm.controls[control].touched){
        this.changePasswordForm.controls[control].updateValueAndValidity();
      }
    });
  }
}