import { Component, OnInit, Input, ElementRef, ViewChild, Output } from '@angular/core';
import { OrdersService } from '@app/_services/orders.service';
import { AuthService } from '@app/auth/auth.service';
import * as _ from 'underscore';
import { UploadService } from '@app/_services/upload.service';
import { EventEmitter } from '@angular/core';

@Component({
  selector: 'app-dronemedia-suborder-overview',
  templateUrl: './dronemedia-suborder-overview.component.html',
  styleUrls: ['./dronemedia-suborder-overview.component.css']
})

export class DronemediaSuborderOverviewComponent implements OnInit {

  allowedFileExt = ['jpg', 'jpeg', 'png', 'JPG']; // allowed file extensions for drone photos
  @Output() setSubmitDisabled = new EventEmitter<boolean>();

  constructor(
    public os: OrdersService,
    public auth: AuthService,
    private us: UploadService
  ) {

  }

  ngOnInit() {
  }

  setSubmitDisabledHanlder(value: boolean) {
    this.setSubmitDisabled.emit(value);
  }

  /**
   * controls disabling of attachment deletion
   */
  deleteAttachmentDisabled() {
    if (
      this.os.currentSuborder.status === 'uploaded' ||
      this.os.currentSuborder.status === 'completed') {
      return true;
    }
    return false;
  }

  /**
 * controls disabling of attachment upload
 */
  isUploadDisabled() {
    if (this.os.currentSuborder.status === 'uploaded' ||
      this.os.currentSuborder.status === 'completed') {
      return true;
    }
    return false;
  }
}
