import { Injectable } from '@angular/core';
import { AngularFirestore, AngularFirestoreCollection, AngularFirestoreDocument} from '@angular/fire/firestore';
import { AngularFireStorage, AngularFireStorageReference, AngularFireUploadTask } from '@angular/fire/storage';
import { finalize } from 'rxjs/operators';
import { Observable } from 'rxjs';
import { OrdersService } from './orders.service';
import * as _ from 'underscore';

@Injectable({
  providedIn: 'root'
})
export class UploadService {

  fileRef: AngularFireStorageReference;
  task: AngularFireUploadTask;
  uploadState: Observable<string>;
  uploadProgress: Observable<number>;

  public downloadURL: any;

  constructor(
    private afStorage: AngularFireStorage,
    private afs: AngularFirestore,
    private os: OrdersService
  ) {}

  upload(event, filePath: any, fileName: any) {
    return new Promise(async resolve => {
      this.fileRef = this.afStorage.ref(`users/${filePath}/${fileName}`);
      this.task = this.fileRef.put(event.target.files[0]);
      this.uploadProgress = await this.task.percentageChanges();
      await this.task.snapshotChanges().pipe(
        finalize(() => {
          this.fileRef.getDownloadURL().subscribe(data => {
            this.downloadURL = data;
            resolve();
          });
        }
      )
      )
      .subscribe();
    });
  }
/**
 * Uploading a file to firebase storage
 * @param filePath is the orderId. The files are stored in a folder named with order id
 */
  uploadToOrder(event, filePath: any, fileName: any) {
    return new Promise(async resolve => {
      this.fileRef = this.afStorage.ref(`orders/${filePath}/${fileName}`);
      this.task = this.fileRef.put(event.target.files[0]);
      this.uploadProgress = await this.task.percentageChanges();
      await this.task.snapshotChanges().pipe(
        finalize(() => {
          this.fileRef.getDownloadURL().subscribe(data => {
            this.downloadURL = data;
            resolve();
          });
        }
      )
      )
      .subscribe();
    });
  }

  uploadtoSubOrder(file, filePath: string, prefix?: string){
    return new Promise(async resolve => {
      let fileRef = this.afStorage.ref(`suborders/${filePath}/${prefix + file.name}`);
      let task = fileRef.put(file);
      await task.snapshotChanges().pipe(
        finalize(() => {
          fileRef.getDownloadURL().subscribe(data => {
            resolve(data);
          },
          error => {
            console.log(error);
          });
        }
      )
      )
      .subscribe();
    });
  }

  deleteAttachment(docType: string, docId: string, subColName: string, subColDocId, filePath: string) {
    this.afs.collection(docType).doc(docId).collection(subColName).doc(subColDocId).delete()
      .then(() => {
        console.log('Document deleted, now deleting actual file');
        this.afStorage.ref(filePath).delete();

      })
      .catch(error => console.log(error));
  }

  getAttachments(docType: string, docId: string, subColName: string) {
    return this.os.getSubcollection(docType, docId, subColName);
  }
}
