import { Component, OnInit, Input, Output } from '@angular/core';
import { AngularFireUploadTask, AngularFireStorage } from '@angular/fire/storage';
import { Observable } from 'rxjs';
import { AngularFirestore } from '@angular/fire/firestore';
import { finalize } from 'rxjs/operators';
import { EventEmitter } from '@angular/core';

@Component({
  selector: 'app-upload-task',
  templateUrl: './upload-task.component.html',
  styleUrls: ['./upload-task.component.css']
})
export class UploadTaskComponent implements OnInit {
  @Input() file: File; // file to be uploaded
  @Input() path: string; // path to upload file on cloud storage
  @Input() parentId: string; // id of the parent record to which subcollection for files is linked
  @Input() parentType: string; // parent type e.g suborders
  @Input() subCollectionName: string; // name for the subcollection
  @Output() removeFileFromQueue = new EventEmitter();
  task: AngularFireUploadTask;
  percentage: Observable<number>;
  snapshot: Observable<any>;
  downloadURL: string;


  constructor(
    private storage: AngularFireStorage,
    private db: AngularFirestore
  ) { }

  ngOnInit() {
    this.startUpload();
  }

  startUpload() {
    // The storage path
    //const path = `folderName/${Date.now()}_${this.file.name}`;
    let self = this;
    // Reference to storage bucket
    const ref = this.storage.ref(this.path);
    // The main task
    this.task = this.storage.upload(this.path, this.file);
    // Progress monitoring
    this.percentage = this.task.percentageChanges();

    this.snapshot = this.task.snapshotChanges().pipe(
      // The file's download URL
      finalize(async () => {
        self.downloadURL = await ref.getDownloadURL().toPromise();
        // add reference to file in the parent's subcollection
        self.db.collection(self.parentType).doc(self.parentId)
        .collection(self.subCollectionName).add({
          download_url: self.downloadURL,
          file_name: self.file.name
        }).then(() => {
          console.log('file uploaded. Now removing from upload queue');
          self.removeFileFromQueue.emit(self.file);
        });
      }),
    );
  }

}
