import { Component, OnInit, Input, Output, EventEmitter, ViewChild, ElementRef, AfterViewInit, AfterContentInit, DoCheck } from '@angular/core';
import { Package } from '@app/models/package';
import * as accounting from 'accounting';
import { FormBuilder, Validators } from '@angular/forms';
import { FileUploader } from 'ng2-file-upload';
import * as _ from 'underscore';
import { forbiddenFileValidator, fileRequiredValidator } from '@app/modals/package-editor/package-editor.component';

@Component({
  selector: 'app-hdphotos-service',
  templateUrl: './hdphotos-service.component.html',
  styleUrls: ['./hdphotos-service.component.css']
})
export class HdphotosServiceComponent implements OnInit, AfterViewInit, AfterContentInit, DoCheck {

  @Input("imoPackage") package: Package;
  @Output() onFormChange = new EventEmitter();
  num_photos_price: any;
  errorMsg: string = '';
  additional_photos: number = 1;
  photosForm: any;
  sum: any;
  total_price: any;
  active2: boolean = false;
  active1: boolean = false;
  public logoUploader: FileUploader = new FileUploader({});
  @ViewChild("logoImg") logoImg: ElementRef<any>;
  logoImgElem: ElementRef;
  oldValidate: boolean;
  isPostProcessing: boolean = false;

  constructor(
    private formBuilder: FormBuilder
  ) { 
    this.photosForm = this.formBuilder.group({
      'additionalPhotos': [0],
      'postProcessing': [false],
      'blurred': [{
        value: false,
        disabled: true
      }],
      'logo': [{
        value: false,
        disabled: true
      }],
      'photographerComments': [''],
      'postprocessingComments': [{
        value: '',
        disabled: true
      }],
      'logoImage': ['']
    });
  }

  get additionalPhotos(){
    return this.photosForm.get('additionalPhotos');
  }

  get postProcessing(){
    return this.photosForm.get('postProcessing');
  }

  get blurred(){
    return this.photosForm.get('blurred');
  }

  get photographerComments(){
    return this.photosForm.get('photographerComments');
  }

  get postprocessingComments(){
    return this.photosForm.get('postprocessingComments');
  }

  get logoImage(){
    return this.photosForm.get('logoImage');
  }

  get logo(){
    return this.photosForm.get('logo');
  }

  ngOnInit() {
    this.package['photo_price'] = accounting.unformat(this.package['price'], ',');
    this.num_photos_price = this.additional_photos * this.package['photo_price'];
    this.package.form = this.photosForm;
    this.package["validate"] = true;
    this.oldValidate = this.package["validate"];
    this.sum = this.package['pack_price'];
    this.package['total_price'] = accounting.formatMoney(this.sum, '', 2, '.', ',');
    this.total_price = this.package['total_price'];
  }

  ngAfterViewInit(){
    let self = this;
    this.logoImgElem = this.logoImg.nativeElement;
    this.photosForm = this.formBuilder.group({
      'additionalPhotos': [0],
      'postProcessing': [false],
      'blurred': [false],
      'logo': [false],
      'photographerComments': [''],
      'postprocessingComments': [{
        value: '',
        disabled: !this.isPostProcessing
      }],
      'logoImage': ['', [
        forbiddenFileValidator(["png"], this.logoImgElem)
      ]]
    });
    self.photosForm.valueChanges.subscribe(() => {
      self.emitFormValues();
    });
  }

  ngAfterContentInit(): void {
    this.calculateSum();
  }

  ngDoCheck(): void {
    if (this.package["validate"] != this.oldValidate) {
      Object.keys(this.photosForm.controls).forEach(control => {
        this.photosForm.controls[control].markAsTouched();
      });
    }
  }

  calculateSum(): void {
    if(this.additionalPhotos.value < 0){
      this.additionalPhotos.setValue(0);
      return;
    }
    this.num_photos_price = accounting.formatMoney(this.additionalPhotos.value * this.package['photo_price'], '', 2, '.', ',');
    this.sum = (this.additionalPhotos.value * this.package['photo_price']) + accounting.unformat(this.package['pack_price'], ',');
    this.package['total_price'] = accounting.formatMoney(this.sum, '', 2, '.', ',');
    this.total_price = this.package['total_price'];
  }

  onLogoCheck(): void {
    if (this.photosForm.controls.logo.value) {
      this.logoImage.setValidators([Validators.required, forbiddenFileValidator(["png"], this.logoImgElem), fileRequiredValidator(this.logoUploader.queue)]);
    } else {
      this.logoImage.setValidators([forbiddenFileValidator(["png"], this.logoImgElem)]);
    }
    this.logoImage.updateValueAndValidity();
    this.package.form = this.photosForm;
  }

  onChange(): void {
    this.package.form = this.photosForm;
    if (this.logoUploader.queue.length > 1) {
      this.logoUploader.queue[0].remove();
    }
    if (this.logoUploader.queue.length > 0 && _.isEmpty(this.photosForm.get('logoImage').value)) {
      this.photosForm.controls['logoImage'].setValue(this.logoUploader.queue[0].file.name);
    }
    this.logoImage.updateValueAndValidity();
  }

  onRemoveFile(file): void {
    file.remove();
    this.logoImage.setValue("");
    this.logoImage.updateValueAndValidity();
  }

  emitFormValues(): void {
    var self = this;
    let formValues = {};
    let fileList = [];
    Object.keys(self.photosForm.controls).forEach(control => {
      if (control !== 'planImages' && control !== 'logoImage') {
        formValues[control] = self.photosForm.get(control).value;
      }else {
        fileList = [];
        self.logoUploader.queue.forEach(entry => fileList.push(entry.file.rawFile));
        formValues[control] = fileList;
      }
    });
    formValues["orderType"] = this.package.service_key;
    formValues['packageNum'] = this.package['package_key'];
    self.onFormChange.emit(formValues);
  }

  onPostProcessingChange(event): void{
    if(!event.checked){
      this.logo.reset(false);
      this.logo.disable()
      this.blurred.reset(false);
      this.blurred.disable();
      this.logoImage.reset();
      this.logoImage.disable();
      this.logoUploader.clearQueue();
      this.postprocessingComments.reset();
      this.postprocessingComments.disable();
    }else{
      this.logo.enable();
      this.blurred.enable();
      this.logoImage.enable();
      this.postprocessingComments.enable();
      this.active2 = true;
    }
    this.emitFormValues();
  }
}
