import { Component, OnInit, AfterViewInit, Output, EventEmitter, Input, ViewChild, ElementRef } from '@angular/core';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { Package } from '@app/models/package';
import { FileUploader } from 'ng2-file-upload';
import { forbiddenFileValidator, fileRequiredValidator } from '@app/modals/package-editor/package-editor.component';
import * as accounting from 'accounting';
import * as _ from 'underscore';

@Component({
  selector: 'app-virtual-staging-service',
  templateUrl: './virtual-staging-service.component.html',
  styleUrls: ['./virtual-staging-service.component.css']
})
export class VirtualStagingServiceComponent implements OnInit, AfterViewInit {
  @Output() onFormChange = new EventEmitter();
  @Input("package") package: Package;
  @ViewChild("uploadFileElem") uploadFileElem: ElementRef<any>;
  public uploader: FileUploader = new FileUploader({});
  filesElem: ElementRef;
  allowedFileExt = ['jpg', 'jpeg', 'png', 'JPG']; // allowed file extensions for drone photos
  oldValidate: boolean;
  vstagingForm: FormGroup;

  constructor(private fb: FormBuilder) {
    this.vstagingForm = this.fb.group({
      description: [''],
      uploadedFiles: ['', [
        Validators.required,
        forbiddenFileValidator(this.allowedFileExt, this.filesElem),
        fileRequiredValidator(this.uploader.queue)
      ]]
    });
  }

  ngOnInit() {
    this.package.form = this.vstagingForm;
    this.package["validate"] = true;
    this.oldValidate = this.package["validate"];
  }

  ngAfterViewInit() {
    this.filesElem = this.uploadFileElem.nativeElement;
    this.vstagingForm.valueChanges.subscribe(() => {
      let formValues = {};
      let fileList = [];
      // handle file uploads
      Object.keys(this.vstagingForm.controls).forEach(control => {
        if (control === 'uploadedFiles') {
          fileList = [];
          this.uploader.queue.forEach(entry => fileList.push(entry.file.rawFile));
          formValues[control] = fileList;
        }
      });
      formValues['description'] = this.vstagingForm.value.description;
      formValues['packageNum'] = this.package['package_key'];
      this.onFormChange.emit(formValues);
    });
  }

  onChange(): void {
    this.package.form = this.vstagingForm;
    const fileListLength = this.uploader.queue.length;
    let lastFile = this.uploader.queue[fileListLength - 1];
    for (let i = 0; i < fileListLength - 1; i++) {
      if (lastFile.file.name === this.uploader.queue[i].file.name && lastFile.file.size === this.uploader.queue[i].file.size) {
        lastFile.remove();
      }
    }
    // fix for Internet Explorer. Manually set the file value
    if (this.uploader.queue.length > 0 && _.isEmpty(this.vstagingForm.get('uploadedFiles').value)) {
      this.vstagingForm.controls['uploadedFiles'].setValue(this.uploader.queue[0].file.name);
    }
    this.vstagingForm.controls.uploadedFiles.updateValueAndValidity();
  }

  ngDoCheck(): void {
    if (this.package["validate"] != this.oldValidate) {
      Object.keys(this.vstagingForm.controls).forEach(control => {
        this.vstagingForm.controls[control].markAsTouched();
      });
    }
  }

  onRemoveFile(file, type?: string): void {
    file.remove();
    this.vstagingForm.controls.uploadedFiles.updateValueAndValidity();
  }

  get uploadedFiles() {
    return this.vstagingForm.get('uploadedFiles');
  }
  get description() {
    return this.vstagingForm.get('description');
  }

  calculatePrice() {
    const unformated_number = accounting.unformat(this.package.price, ',');
    const total_price = unformated_number * this.uploader.queue.length;
    this.package['total_price'] = accounting.formatMoney(total_price, '', 2, '.', ',');
    // return formatted price
    return this.package['total_price'];
  }
}
