import { Component, OnInit, Input } from '@angular/core';
import { Package } from '../models/package';
import { ServicesComponent } from '@app/services/services.component';

import { MatDialog } from '@angular/material';
import { PackageEditorComponent } from '@app/modals/package-editor/package-editor.component';
import { AngularFirestore } from '@angular/fire/firestore';
import { ConfigurationService } from '@app/_services/configuration.service';
import { UsersService } from '@app/_services/users.service';

@Component({
  selector: 'app-package',
  templateUrl: './package.component.html',
  styleUrls: ['./package.component.css']
})
export class PackageComponent implements OnInit {

  @Input('package') package: Package;
  @Input() servicesComponent: ServicesComponent; // reference of the parent services component
  @Input() selectedPackages: Package[] = [];
  @Input() selectedServices: string[] = [];
  success: boolean;

  constructor(
    public dialog: MatDialog,
    private afs: AngularFirestore,
    public conf: ConfigurationService,
    private userService: UsersService
  ) { }

  ngOnInit() {
    // console.log(this.servicesComponent.selectedPackages);
  }

  async packageSelectHandler(service, event) {
    // if the package is selected, insert in array only if it not already exists
    /* if (event.target.checked && !this.servicesComponent.selectedPackages.includes(option)) {
      this.servicesComponent.selectedPackages.push(option);
    } else if (!event.target.checked && this.servicesComponent.selectedPackages.includes(option)) {
      // remove from array if unchecked
      this.servicesComponent.selectedPackages.splice(this.servicesComponent.selectedPackages.indexOf(option), 1);
    } */
    let option = service.service_key;
    if (!service.disabled) {
      if (this.servicesComponent.state === '3') {
        if (this.servicesComponent.selectedPackages.indexOf(service.package_key) < 0) {
          this.servicesComponent.selectedPackages.push(service.package_key);
        } else {
          this.servicesComponent.selectedPackages.splice(this.servicesComponent.selectedPackages.indexOf(service.package_key), 1);
        }
        this.servicesComponent.packageSelectHandler();
        return;
      }
      if (this.servicesComponent.selectedServices.indexOf(option) < 0) {
        this.servicesComponent.selectedServices.push(option);
        this.servicesComponent.cartItems.push(this.servicesComponent.availablePackages.filter(service => service.service_key === option)[0]);
      } else {
        let i = 0;
        for (; i < this.servicesComponent.cartItems.length; i++) {
          if (this.servicesComponent.cartItems[i].service_key === option) {
            break;
          }
        }
        this.servicesComponent.selectedServices.splice(this.servicesComponent.selectedServices.indexOf(option), 1);
        this.servicesComponent.cartItems.splice(i, 1);
      }
      this.userService.cartItems = this.servicesComponent.cartItems;
      this.userService.selectedServices = this.servicesComponent.selectedServices;
    } else {
      try {
        let updateDisabledServices = {};
        if (this.conf.disabledServices[service.title]) {
          updateDisabledServices[service.title] = this.conf.disabledServices[service.title] + 1;
        } else {
          updateDisabledServices[service.title] = 1;
        }
        await this.afs.collection('configuration').doc('Lt8fpXKYGVNy6qjpi0hU/').update(updateDisabledServices);
        this.success = true;
        setTimeout(() => {
          this.success = false;
        },
          1500);
      } catch (err) {
        console.error(err);
      }
    }
  }
}
